  }

  .main {
    width: 300px;
    height: 300px;
    background-color: #1e2231;
    border-color: rgb(0, 0, 0);
    border-radius: 50%;
    border-width: 2px;
    border-style: solid;
    display: grid;
    justify-content: center;
    align-content: center;
  }

  .main img{
    width: 70px;
  }
  .main img.react{
    width: 50px;
  }

  .main > * {
    grid-column: 1;
    grid-row: 1;
  }

  .main .theSmiley {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    /* animation: rotateLeft 15s linear infinite; */
  }

  .orange img,  .orange i{
    width: 50px;
    display: block;
    margin: 0 auto;
    margin-top: 50px;
  }

  .item {
    -webkit-animation: spinAround 45s linear infinite;
            animation: spinAround 45s linear infinite;
  }

  .main > * > img {
    /* animation: rotate 5s linear infinite; */
  }
  .main > * > i {
    /* animation: rotate 5s linear infinite; */
  }

  @-webkit-keyframes rotateLeft {
    from {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
    to {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
  }

  @keyframes rotateLeft {
    from {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
    to {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
  }

  @-webkit-keyframes spinAround {
    from {
      -webkit-transform: rotate(0deg) translate(140px) scale(.7);
              transform: rotate(0deg) translate(140px) scale(.7);
    }
    to {
      -webkit-transform: rotate(360deg) translate(140px) scale(.7);
              transform: rotate(360deg) translate(140px) scale(.7);
    }
  }

  @keyframes spinAround {
    from {
      -webkit-transform: rotate(0deg) translate(140px) scale(.7);
              transform: rotate(0deg) translate(140px) scale(.7);
    }
    to {
      -webkit-transform: rotate(360deg) translate(140px) scale(.7);
              transform: rotate(360deg) translate(140px) scale(.7);
    }
  }



.main .item img, .main .item i{
    -webkit-animation: rotate 15s linear infinite;
            animation: rotate 15s linear infinite;
  }
  
  @-webkit-keyframes rotate {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes rotate {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

